import React from 'react';
import './GoodsList.scss';

type Props = {
  goods: string[],
};

export const GoodList: React.FC<Props> = ({ goods }) => (
  <ul className="goods-list">
    {goods.map(good => (
      <li key={good} className="goods-list__good">
        {good}
      </li>
    ))}
  </ul>
);
